body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.navigation ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333333;
}

.navigation li {
  float: left;
}

.navigation li a {
  display: block;
  text-align: center;
  padding: 16px;
  text-decoration: none;
}

.navigation li a:hover {
  background-color: #111111;
}

.navigation li button {
  text-align: center;
  padding: 8px;
  border: none;
  background: none;
  cursor: pointer;
}

.navigation li button:hover {
  background-color: #111111;
}

.navigation--secondary ul {
  background-color: #666666;
}

.navigation__item {
  color: white;
}

.navigation__item--active {
  color: red;
}

.error {
  padding: 8px;
  background-color: red;
  font-weight: bold;
}

.error--close {
  position: absolute;
  right: 2px;
  top: 86;
  cursor: pointer;
}

.container {
  padding: 1em;
}

.box {
  border: 1px solid black;
  padding: 5px;
}

.box--active {
  border: 1px solid red;
}

.row {
  display: flex;
}

.column {
  display: flex;
  flex-direction: column;
}

.fill {
  width: 100%;
  height: 100%;
}
