.grid {
  flex: 1;
}

.grid--draggable {
  cursor: move;
}

.grid__item--close {
  position: absolute;
  right: 2px;
  top: 0;
  cursor: pointer;
}

.grid__item--edit {
  position: absolute;
  right: 50px;
  top: 0;
  cursor: pointer;
}

.grid__item--updating {
  position: absolute;
  right: 150px;
  top: 0;
}
